// Import JavaScript modules
import { UA3 } from './config.js';
import { registerSettings } from './settings.js';
import { preloadTemplates } from './preloadTemplates.js';
import ua3Combat from './combat/combat.js';
import ua3CombatTracker from './combat/combatTracker.js';
import ua3Combatant from './combat/combatant.js';

// Import entities
import { ua3Actor } from './actor/entity.js';
import { ua3Item } from './item/entity.js';

// Import sheets
import { ua3ActorSheet } from './actor/sheet.js';
import { ua3ItemSheet } from './item/sheet.js';
import { ua3ArchetypeSheet } from './archetype/sheet.js';
import { ua3SchoolSheet } from './school/sheet.js';

// Import helpers
import * as dice from './dice.js';

// Handlebars
import { registerHelpers } from './handlebarHelpers.js';

// Initialize system
Hooks.once('init', async () => {
  console.log(`UA3 | Initializing UA3e System\n${UA3.ASCII}`);

  // Create a namespace within the game global
  game.ua3 = {
    applications: {
      /*
      AbilityUseDialog,
      ActorSheetFlags,
      ActorSheet5eCharacter,
      ActorSheet5eNPC,
      ActorSheet5eVehicle,
      */
      ua3ActorSheet,
      ua3ItemSheet,
      ua3ArchetypeSheet,
      ua3SchoolSheet,
    },
    canvas: {
      //AbilityTemplate
    },
    config: UA3,
    dice: dice,
    entities: {
      ua3Actor,
      ua3Item,
      //TokenDocument5e,
      //Token5e,
    },
    //macros: macros,
    //migrations: migrations,
    //rollItemMacro: macros.rollItemMacro
  };

  // Assign custom classes and constants here
  CONFIG.UA3 = UA3;
  CONFIG.Actor.documentClass = ua3Actor;
  CONFIG.Item.documentClass = ua3Item;
  CONFIG.Combat.documentClass = ua3Combat;
  CONFIG.Combatant.documentClass = ua3Combatant;
  CONFIG.ui.combat = ua3CombatTracker;

  //CONFIG.Token.documentClass = TokenDocument5e;
  //CONFIG.Token.objectClass = Token5e;
  CONFIG.time.roundTime = 6;

  CONFIG.Dice.DUARoll = dice.DUARoll;

  // Register custom system settings
  registerSettings();

  // Patch Core Functions
  CONFIG.Combat.initiative.formula = '1d0 + @system.initiative.value';
  //Combatant.prototype._getInitiativeFormula = _getInitiativeFormula;

  // Register Roll Extensions
  CONFIG.Dice.rolls.push(dice.DUARoll);

  // Handlebars
  registerHelpers();
  await preloadTemplates();

  // Register custom sheets
  Actors.unregisterSheet('core', ActorSheet);
  Actors.registerSheet('ua3', ua3ActorSheet, {
    makeDefault: true,
  });

  Items.unregisterSheet('core', ItemSheet);
  Items.registerSheet('ua3', ua3ItemSheet, {
    makeDefault: true,
  });
  Items.registerSheet('ua3', ua3ArchetypeSheet, {
    types: ['archetype'],
    makeDefault: true,
  });
  Items.registerSheet('ua3', ua3SchoolSheet, {
    types: ['school'],
    makeDefault: true,
  });
});

// Setup system
Hooks.once('setup', async () => {
  // Do anything after initialization but before ready
});

// When ready
Hooks.once('ready', async () => {
  // Do anything once the system is ready
});

// Add any additional hooks if necessary
Hooks.on('renderua3ActorSheet', (app, html, data) => {
  if (data.actor.type == 'character') {
    let gauges = data.actor.system.gauge || {};
    for (var key in gauges) {
      let gauge = gauges[key];
      app._setStressGauge(html, key, gauge.hardened, gauge.failed);
    }
  }
});

Hooks.on('renderua3ItemSheet', (app, html, data) => {
  app._setPostUpdateRender(app, html, data);
});

Hooks.on('renderChatMessage', (msg, html, data) => {
  if (msg.flags.ua3?.type === 'DUARoll') {
    dice.DUARoll.formatChat(msg, html);
  }
});

Hooks.on('applyActiveEffect', (actor, change) => {
  actor.applyCustomEffect(change);
});

