export default class ua3Combatant extends Combatant {
  prepareDerivedData() {
    super.prepareDerivedData();
    //this.flags = { isNameKnown: true };

    if (!this.isOwner) {
      if (!this.hasPlayerOwner && this.token.displayName == CONST.TOKEN_DISPLAY_MODES.OWNER) {
        this.name = '';
      }
    }
  }
}
